@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes navigationServicesAppear {
    from { transform: scale(0.2); }
    to { transform: scale(1); }
}

@keyframes navigationFixedTransitionSlideDown {
    from { transform: translateY(-100%); }
    to { transform: translateY(0); }
}

@keyframes navigationReturnToTopFadeIn {
    from { transform: scale(0.6); opacity: 0; }
    to { transform: scale(1);  opacity: 1; pointer-events: auto; }
}

@keyframes navigationReturnToTopFadeOut {
    from { transform: scale(1);  opacity: 1; pointer-events: auto; }
    to { transform: scale(0.6); opacity: 0; }
}
