html.docs {
  min-height: 150vh; }
  html.docs body {
    background-color: #ffffff; }
  html.docs .content-container {
    min-height: 400px;
    border-radius: 3px;
    margin-bottom: 10px;
    padding: 10px;
    background: #ffffff;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif; }
    html.docs .content-container .docs-page::before {
      display: table;
      content: ''; }
    html.docs .content-container .docs-page::after {
      display: table;
      clear: both;
      content: ''; }
    html.docs .content-container .docs-page-header {
      margin: -11px -10px 10px;
      border-bottom: 1px solid #e5e5e5; }
      html.docs .content-container .docs-page-header-title {
        margin: 0;
        padding: 10px;
        line-height: 40px; }
    html.docs .content-container .docs-page-toggle {
      float: right; }
      html.docs .content-container .docs-page-toggle button {
        border: 0;
        bottom: -1px;
        background: 0;
        position: relative;
        padding: 10px;
        line-height: 40px; }
        html.docs .content-container .docs-page-toggle button:focus, html.docs .content-container .docs-page-toggle button:active {
          border: 0;
          outline: 0;
          box-shadow: none; }
        html.docs .content-container .docs-page-toggle button.active {
