.tooltipped {
    position: relative;

    // This is the tooltip bubble
    &::after {
        position: absolute;
        z-index: 1000000;
        display: none;
        padding: 5px 8px;
        // Not an operator, font
        // scss-lint:disable SpaceAroundOperator
        font: normal normal 11px/1.5 $body-font;
        // scss-lint:enable SpaceAroundOperator
        color: $tooltip-text-color;
        text-align: center;
        text-decoration: none;
        text-shadow: none;
        text-transform: none;
        letter-spacing: normal;
        word-wrap: break-word;
        white-space: pre;
        pointer-events: none;
        content: attr(aria-label);
        background: $tooltip-background-color;
        border-radius: 3px;
        -webkit-font-smoothing: subpixel-antialiased;
    }

    // This is the tooltip arrow
    &::before {
        position: absolute;
        z-index: 1000001;
        display: none;
        width: 0;
        height: 0;
        color: $tooltip-background-color;
        pointer-events: none;
        content: '';
        border: 5px solid transparent;
    }

    // This will indicate when we'll activate the tooltip
    &:hover,
    &:active,
    &:focus {
        &::before,
        &::after {
            display: inline-block;
            text-decoration: none;
        }
    }

    &-success {
        @include colorize-tooltip($tooltip-success-text-color, $tooltip-success-background-color);
    }

    &-error {
        @include colorize-tooltip($tooltip-error-text-color, $tooltip-error-background-color);
    }

    &-warning {
        @include colorize-tooltip($tooltip-warning-text-color, $tooltip-warning-background-color);
    }

    &-multiline:hover,
    &-multiline:active,
    &-multiline:focus {
        &::after {
            display: table-cell;
        }
    }

    // Tooltipped south
    &-s,
    &-se,
    &-sw {
        &::after {
            top: 100%;
            right: 50%;
            margin-top: 5px;
        }

        &::before {
            top: auto;
            right: 50%;
            bottom: -5px;
            margin-right: -5px;
            border-bottom-color: $tooltip-background-color;
        }
    }

    &-se {
        &::after {
            right: auto;
            left: 50%;
            margin-left: -15px;
        }
    }

    &-sw::after {
        margin-right: -15px;
    }

    // Tooltips above the object
    &-n,
    &-ne,
    &-nw {
        &::after {
            right: 50%;
            bottom: 100%;
            margin-bottom: 5px;
        }

        &::before {
            top: -5px;
            right: 50%;
            bottom: auto;
            margin-right: -5px;
            border-top-color: $tooltip-background-color;
        }
    }

    &-ne {
        &::after {
            right: auto;
            left: 50%;
            margin-left: -15px;
        }
    }

    &-nw::after {
        margin-right: -15px;
    }

    // Move the tooltip body to the center of the object.
    &-s::after,
    &-n::after {
        transform: translateX(50%);
    }

    // Tooltipped to the left
    &-w {
        &::after {
            right: 100%;
            bottom: 50%;
            margin-right: 5px;
            transform: translateY(50%);
        }

        &::before {
            top: 50%;
            bottom: 50%;
            left: -5px;
            margin-top: -5px;
            border-left-color: $tooltip-background-color;
        }
    }

    // tooltipped to the right
    &-e {
        &::after {
            bottom: 50%;
            left: 100%;
            margin-left: 5px;
            transform: translateY(50%);
        }

        &::before {
            top: 50%;
            right: -5px;
            bottom: 50%;
            margin-top: -5px;
            border-right-color: $tooltip-background-color;
        }
    }

    // Multiline tooltips
    //
    // `.tooltipped-multiline` Add this class when you have long content.
    // The downside is you cannot preformat the text with newlines and `[w,e]`
    // are always `$multiline-max-width` wide.
    &-multiline {
        &::after {
            width: max-content;
            max-width: $multiline-max-width;
            word-break: break-word;
            word-wrap: normal;
            white-space: pre-line;
            border-collapse: separate;
        }

        &.tooltipped-s::after,
        &.tooltipped-n::after {
            right: auto;
            left: 50%;
            transform: translateX(-50%);
        }

        &.tooltipped-w::after,
        &.tooltipped-e::after {
            right: 100%;
        }
    }

    @media screen and (min-width:0\0) {
        // IE9 and IE10 rule sets go here
        .tooltipped-multiline::after {
            width: $multiline-max-width;
        }
    }

    // Sticky tooltips
    //
    // Always show the tooltip.
    &-sticky {
        &::before,
        &::after {
            display: inline-block;
        }

        &.tooltipped-multiline {
            &::after {
                display: table-cell;
            }
        }
    }
}
